/* eslint-disable @typescript-eslint/no-unused-vars */
import { AxiosError } from "axios";
import { cellProvider, MIN_VERSION_SUPPORTED } from "../constants";
import { failCellIcon } from "../constants";

const statusToMessage = (status: number) => {
  if (status === 401) {
    return "InvalidAPIKey";
  }
  if (status === 403) {
    return "AccessDenied";
  }
  if (status === 500) {
    return "ServerBusy";
  }
  if (status === 429) {
    return "UsageLimitExceeded";
  }
  return null;
};

const returnErrorEntity = <
  T extends Excel.EntityCellValue & {
    properties: { [key: string]: Excel.EntityPropertyType } & {
      success: { type: Excel.CellValueType; basicValue: boolean };
      error: { type: Excel.CellValueType; basicValue: string };
      httpStatus?: { type: Excel.CellValueType; basicValue: number };
      queriedAt?: { type: Excel.CellValueType; basicValue: string };
    };
  },
>(
  error: AxiosError | unknown,
  params: Omit<T["properties"], "success" | "error" | "httpStatus" | "queriedAt">
): T => {
  let queriedAt: string | null = null;
  let message = error instanceof Error ? error.message : "Unknown error";
  let httpStatus = null;

  if (error instanceof AxiosError) {
    const statusErrorMessage = statusToMessage(error.status);
    if (statusErrorMessage) {
      message = statusErrorMessage;
    }
    queriedAt = error.config["metadata"].start.toISOString();
    httpStatus = error.status;
  }

  return {
    type: Excel.CellValueType.entity,
    text: `#${message}!`,
    provider: cellProvider,
    properties: {
      ...params,
      success: {
        type: Excel.CellValueType.boolean,
        basicValue: false,
      },
      error: {
        type: Excel.CellValueType.string,
        basicValue: message,
      },
      ...(httpStatus
        ? {
            httpStatus: {
              type: Excel.CellValueType.double,
              basicValue: httpStatus,
            },
          }
        : {}),
      ...(queriedAt
        ? {
            queriedAt: {
              type: Excel.CellValueType.string,
              basicValue: queriedAt,
            },
          }
        : {}),
    },
    layouts: {
      compact: {
        icon: failCellIcon,
      },
    },
  } as T;
};

